import React, { useEffect, useState } from 'react';
import * as Icon from 'react-bootstrap-icons';
import * as Utility from '../../scripts/utility';

import '../../css/performance.css';

const resolution = 100; // 100 milliseconds

export const Performance = props => {
	const [time, setTime] = useState(new Date());
	const [speed, setSpeed] = useState(null);
	const [run, setRun] = useState({start: null, end: null, topSpeed: null, to60: null, times: [], speeds: []});

	const updateTime = () => {
		setTime(new Date());
	};

	useEffect(() => {
		const interval = setInterval(updateTime, 1000); // 1 second

		return () => clearInterval(interval);
	}, []);

	const logTime = () => {
		navigator.geolocation.getCurrentPosition(location => {
			setRun(currentRun => {
				if(currentRun.end) return{...currentRun};

				const now = Date.now();
				const currentSpeed = location.coords.speed;

				setSpeed(currentSpeed);
				setTimeout(logTime, resolution);

				return {
					...currentRun,
					start: currentRun.start || now,
					topSpeed: Math.max(currentSpeed, currentRun.topSpeed),
					to60: Utility.mpsToMPH(currentSpeed) >= 60 && !currentRun.to60 ? currentSpeed : currentRun.to60,
					// times: [...currentRun.times, now],
					// speeds: [...currentRun.speeds, currentSpeed || 0]
				};
			});
		}, err => {
			console.error(err);
			setSpeed(-1);
			endTimer();
		}, {enableHighAccuracy: true});
	}

	const endTimer = () => setRun(currentRun => ({ ...currentRun, end: Date.now() }));

	const resetTimer = () => setRun({start: null, end: null, topSpeed: null, to60: null, times: [], speeds: []});

	const getCurrentSpeed = () => String(Number(speed).toFixed(0)).padStart(3, '0');

	const getTopSpeed = () => Utility.mpsToMPH(run.topSpeed || 0).toFixed(1);

	const getAcceleration = () => run.to60 ? `${Utility.mpsToMPH(run.to60).toFixed(2)} s` : 'N/A';

	const getDisplayTime = () => {
		const now = Date.now();
		const duration = (run.end || now) - (run.start || now);
		const minutes = String(Math.floor(duration / 1000 / 60) || 0).padStart(2, '0');
		const seconds = String(Math.floor(duration / 1000 % 60) || 0).padStart(2, '0');
		const milliseconds = String(duration % 1000 || 0).padStart(3, '0');

		return `${minutes}:${seconds}.${milliseconds}`;
	};

	const updatePosition = () => {
		if(!navigator.geolocation) return;
		
		navigator.geolocation.getCurrentPosition(location => {
			setSpeed(location.coords.speed);
		}, err => {
			console.error(err);
			alert(`${err.code} - ${err.message}`);
			setSpeed(-1);
		}, {enableHighAccuracy: true});
	};

	const askForLocationPermission = () => {
		// updatePosition();
		if ( navigator.permissions && navigator.permissions.query) {
			navigator.permissions.query({ name: 'geolocation' })
				.then(result => {
					if ( result.state === 'granted' || result.state === 'prompt' ) {
						updatePosition();
					}
				})
				.catch(err => {
					console.log(err);
					alert(`${err.code} - ${err.message}`);
					setSpeed(-1);
				});
		}
		else if (navigator.geolocation) {
			updatePosition();
		}
	};

	useEffect(askForLocationPermission, []);

	if(speed === -1) return (
		<div id="weather">
			<Icon.PinMap id="weather-icon" />
			<div id="weather-description">
					Error getting location
			</div>
		</div>
	);

	return (
		<div id="performance">
			<div id="performance-speed">
				{getCurrentSpeed()}
			</div>
			<div id="performance-stats">
				<div>
					<span>{getTopSpeed()} mph</span>
					<span>Top Spd</span>
				</div>
				<div>
					<span>{getAcceleration()}</span>
					<span>0-60</span>
				</div>
				{/* <div>
					<span>1.2 G</span>
					<span>Top Acc</span>
				</div>
				<div>
					<span>12.3 s</span>
					<span>0-120</span>
				</div> */}
			</div>
			<div id="performance-timer">
				{getDisplayTime()}
			</div>
			<div id="performance-buttons">
				{
					run.start && !run.end ?
						<button type="button" onClick={endTimer}>
							Stop
						</button> :
						<button type="button" onClick={logTime}>
							Start
						</button>
				}
				<button type="button" onClick={resetTimer}>
					Reset
				</button>
			</div>
		</div>
	);
}

export default Performance;
